<template>
  <div>
    <v-container>
      <v-layout row wrap>
        <v-flex xs12 md12 class="pr-1">
          <v-card class="mb-2">
            <v-card-title class="headline font-weight-regular blue-grey white--text">Audit List</v-card-title>
            <v-card-text>
              <v-list>
                <v-expansion-panel focusable>
                  <v-expansion-panel-content v-for="facility in distinctFacilities" :key="facility">
                    <div slot="header">{{ facility }}</div>
                    <v-expansion-panel>
                      <v-expansion-panel-content
                        v-for="(practice, index) in distinctPracticesPerFacility(facility)"
                        :key="index"
                      >
                        <div slot="header" class="indent-1">{{ practice }}</div>

                        <v-expansion-panel>
                          <v-expansion-panel-content
                            v-for="(practice_audits, index) in distinctPracticeAuditsPerFacility(facility, practice)"
                            :key="index"
                          >
                            <div slot="header" class="indent-2">Audit Start - {{ practice_audits }}</div>

                            <v-expansion-panel>
                              <v-expansion-panel-content
                                v-for="(physician, index) in distinctPhysiciansPerPractice(facility, practice, practice_audits)"
                                :key="index"
                                class="grey lighten-2"
                              >
                                <div slot="header" class="indent-3">{{ physician }}</div>

                                <v-expansion-panel>
                                  <v-expansion-panel-content
                                    hide-actions
                                    v-for="(item, index) in audits"
                                    :key="index"
                                    v-if="item.facility === facility && item.group_practice === practice && item.practice_created_date === practice_audits"
                                  >
                                    <div
                                      slot="header"
                                      class="indent-3"
                                      @click="auditPatient(item.audit_id)"
                                    >
                                      <!-- {{ item.physician_name }} - {{ item.patient_name }} - {{ item.service_date }} -->
                                      <v-layout row wrap>
                                        <!-- <v-flex xs12 md4>Physician: {{ item.physician_name }}</v-flex> -->
                                        <v-flex xs12 md6>Patient: {{ item.patient_name }}</v-flex>
                                        <v-flex xs12 md6>Service Date: {{ item.service_date }}</v-flex>
                                      </v-layout>
                                    </div>
                                  </v-expansion-panel-content>
                                </v-expansion-panel>
                              </v-expansion-panel-content>
                            </v-expansion-panel>
                          </v-expansion-panel-content>
                        </v-expansion-panel>
                      </v-expansion-panel-content>
                    </v-expansion-panel>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-list>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import Config from "../config";
import Router from "../router";
import httpService from "../httpService";

export default {
  name: "Main",
  components: {},
  data: () => ({
    audits: []
  }),
  async mounted() {
    //this.$store.dispatch("FETCH_AUDITS", { auditor: this.currentUser.id });
    httpService.get(
      `${Config.apiRoot}/audit/auditor/${this.currentUser.id}`,
      (status, data) => {
        // sort by patient for display on list.  speed?
        this.audits = data.sort((a, b) => {
          const nameA = a.patient_name.toUpperCase();
          const nameB = b.patient_name.toUpperCase();
          if (nameA < nameB) {
            return -1;
          }
          if (nameA > nameB) {
            return 1;
          }
          // names must be equal
          return 0;
        });
        this.audits;
      }
    );
  },
  computed: {
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
    distinctFacilities() {
      return [...new Set(this.audits.map(obj => obj.facility))];
    }
  },
  methods: {
    auditPatient(id) {
      // user selected a record to work on
      Router.push({ name: "audit-record", params: { audit_id: id } });
    },
    distinctPracticesPerFacility(facility) {
      // distinct practices for the 2nd grouping of the list
      return [
        ...new Set(
          this.audits
            .filter(obj => obj.facility === facility)
            .map(obj => obj.group_practice)
        )
      ];
    },
    distinctPracticeAuditsPerFacility(facility, practice) {
      // distinct audit dates for practices for the 3rd grouping of the list
      return [
        ...new Set(
          this.audits
            .filter(
              obj =>
                obj.facility === facility && obj.group_practice === practice
            )
            .map(obj => obj.practice_created_date)
        )
      ];
    },
    distinctPhysiciansPerPractice(facility, practice, created_date) {
      // distinct physicians for the 4th grouping of the list
      return [
        ...new Set(
          this.audits
            .filter(
              obj =>
                obj.facility === facility &&
                obj.group_practice === practice &&
                obj.practice_created_date === created_date
            )
            .map(obj => obj.physician_name)
        )
      ];
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}

.indent-1 {
  margin-left: 15px;
}

.indent-2 {
  margin-left: 30px;
}

.indent-3 {
  margin-left: 45px;
}

.indent-4 {
  margin-left: 60px;
}
</style>
